/* Footer */

/* * {
    border: 1px solid black;
} */

footer{
    bottom: 0;
    width: 100%;
    min-height: 2.5rem;
    font-size: 1.5rem;
    position: absolute;
    padding-top: 1.5rem;
    padding-bottom: 1rem;
    color: var(--fg-color);
    display: inline-flexbox;
    background: var(--ftr-bg-color);
    box-shadow: 0px -2px 8px -4px rgb(0 0 0 / 80%);
}

#footerContainer {
    width: 75%;
    margin: 0 auto;
    text-align: center;
}

#footerContainer #socialLinks {
    width: 12%;
    display: inline-flex;
}

#footerContainer #socialLinks li {
    margin: 0 auto;
}

#footerContainer #socialLinks img {
    width: 2rem;
    height: 2rem;
}

#footerContainer .linkContainer {
    width: 25%;
    border-top: 2px solid black;
}

#footerContainer .linkContainer li {
    margin: 0 auto;
    font-size: 60%;
}

#footerContainer .linkContainer .link {
    font-weight: 500;
}

#footerContainer .linkContainer .link:hover {
    color: red;
}

#footerContainer #copyright{
    color: white;
    margin-top: .5%;
    font-size: 55%;
    cursor: default;
    font-weight: 600;
    text-shadow: 4px 4px 2px black;

}

@media screen and (max-width: 1420px) {
    #footerContainer {
        width: 60%;
    }

    #footerContainer #socialLinks {
        width: 20%;
    }

}

@media screen and (max-width: 1280px) {
    #footerContainer {
        width: 100%;
    }

    #footerContainer #socialLinks {
        width: 20%;
    }

}

@media screen and (max-width: 800px) {
    #footerContainer {
        width: 100%;
    }

    #footerContainer #socialLinks {
        width: 20%;
    }

}

@media screen and (max-width: 550px) {
    #footerContainer {
        width: 100%;
    }

    #footerContainer #socialLinks {
        width: 45%;
    }
}