/* Home */

/* * {
    border: 1px solid black;
} */

#home {
    padding: 0;
}

#home .section-1 {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-blend-mode: multiply;
    background-color:  rgba(0, 0, 0, .4);
    background-image: url(./../images/backgrounds/glacierNationalPark_PeterPeck.jpg);
}

#home .section-1 p{
    float: right;
    cursor: default;
    margin-top: 65vh;
    font-size: .95em;
    margin-right: 5%;
}

#home .intro {
    width: 45%;
    margin: 0 auto;
    cursor: default; 
    text-shadow: 4px 4px 8px black;
    font-family: Lato, 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif; 
}

#home .intro h1 {
    font-size: 5rem;
    font-family:'Times New Roman', Times, serif;
}

#home .intro h2 {
    margin-left: 20%;
}


@media screen and (max-height: 500px) {
    #home .intro h1 {
        font-size: 3rem;
    }
    
    #home .intro h2 {
        margin-left: 20%;
    }

    #home .section1 p {
        margin-top: 35vh;
    }
}  

