/* SoF */
#sof {
    cursor: default;
    text-align: center;
}

#sof .bg-overlay {
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, .8);
}

#sof .container {
    padding: 1%;
    z-index: 20;
    height: 60vh;
    width: 70vw;
    color: Linen;
    overflow: auto;
    margin: 0 auto;
    margin-top: 20vh;
    border-radius: 2%;
    position: relative;
    background: #303233;
    border: 4px solid gray;
    text-shadow: 2px 2px 6px black;
    box-shadow: 10px 10px 8px black;
}

#sof .closeDialog {
    float: right;
    color: white;
    font-size: 110%;
    font-weight: 600;
    margin-right: 5%;
    text-align: center;
    text-decoration: none;
}

#sof h1 {
    padding: 3%;
    text-underline-offset: .4em;
    text-decoration: 3px underline rgb(177, 134, 27);
}

#sof p {
    color: rgb(197, 196, 194);
}

#sof .txtBody {
    padding: 1%;
    font-size: 95%;
    padding-left: 3%;
    padding-right: 3%;
    text-align: left;
    line-height: 2em;
    word-spacing: .25em;
}

#sof .txtFtr {
    padding: 1%;
    margin-right: 8%;
    text-align: right;
}