/* About */

/* * {
    border: 1px solid gray;
} */

#about {
    width: 100%;
    cursor: default;
    text-align: center;
}

#about .container {
    padding: 0;
    color: Linen;
    height: 60vh;
    margin-top: 15vh;
    text-shadow: 2px 2px 8px black;
    background: rgba(80, 76, 76, .8);
}

#about .section {
    margin: 4%;
    height: 50%;
    display: inline-block;
}

#about .section h1 {
    width: 15em;
    text-align: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

#about .s1 p {
    width: 46em;
    padding: 1em;
    margin-left: 10%;
    line-height: 2em;
    text-align: start;
    word-spacing: .15em;
}

#about .section a {
    color: rgba(0,205,205, 1);
    padding-bottom: .25em;
    text-decoration: none;
    border-bottom: 2px solid goldenrod;
}

#about .s2 {
    width: 260px;
    height: 300px;
    padding-left: .1%;
}

#about .section #profileImg {
    max-width: 260px;
    max-height: 300px;
    border-radius: 50%;
    box-shadow: .01px .01px 1px black;
}

#about #faith {
    width: 24%;
    cursor: pointer;
    padding-bottom: .25em;
    color: rgba(0,205,205, 1);
    text-shadow:  4px 4px 8px black;
    border-bottom: 2px solid goldenrod;
}

#about .container #faith:hover  {
    text-shadow: inset 1px 1px 1px black;
}

@media screen and (max-width: 1600px) {
    #about .container {
        height: 65vh;
    }

    #about .section {
        margin: 4%;
        height: 48%;
    }

    #about .section #profileImg {
        display: none;
    }
}

@media screen and (max-width: 750px) {
    #about .container {
        height: 100vh;
    }

    #about .section {
        margin: 0;
        display: block;
        padding-top: 3%;
        font-size: 95%;
    }

    #about .section h1 {
        width: 10em;
        margin-top: 20%;
        margin-left: 5%;
        text-align: start;
    }

    #about .section p{
        width: 98vw;
        margin-left: 0;
    }

    #about .s2 {
        margin-left: 15%;
    }
}

@media screen and (max-width: 400px) {
    #about .section {
        font-size: 82%;
    }
}

@media screen and (max-height: 500px) {
    #about {
        margin-top: 15%;
        padding-bottom: 40%;
    }

    #about .section #profileImg {
        display: none;
    }
}