/* Hamburger Menu */


#hamburger-input{
    display: none;
  }
  
  #hamburger-menu {
    z-index: 50;
    top: 4vh;
    right: 20px;
    width: 1.5em;
    height: 1.5em;
    margin: 0;
    padding: 0;
    display: none;
    border: none;
    cursor: pointer;
    position: fixed;
    border-radius: 2%;
    background: linear-gradient(
        to bottom, 
        black, black 15%, 
        transparent 15%, transparent 40%, 
        black 40%, black 60%, 
        transparent 60%, transparent 85%, 
        black 85%, black 100%
    );
  }

  #sidebar-menu {
    border: .3em solid black;
  }
  
  #hamburger-menu #sidebar-menu {
      border-radius: 2%;
      position: fixed;
      transition: 0.3s;
      padding: 0px 10px;
      visibility: hidden;
      box-sizing: border-box;
      background: rgba(54,54,54);
      right: var(--hamburger-hidden);
      width: var(--hamburger-width);
      height: var(--hamburger-height);
      top: calc(var(--nav-height) * .75);
  }
  
  #hamburger-menu h3 {
    float: right;
    font-weight: 700;
  }
  
  #hamburger-menu ul {
    margin-top: 5vh;
    text-align: center;
    padding-top: 10%;
    padding-left: 0px;
    padding-bottom: 10%;
  }
  
  #hamburger-menu .link {
    font-size: 1.4rem;
    line-height: 3rem;
  }
  
  #hamburger-input:checked + #hamburger-menu #sidebar-menu {
    visibility: visible;
    right: var(--hamburger-show);
  }

@media screen and (max-width: 760px) {
    #hamburger-menu {
        display: inline-flex;
    }
}

@media screen and (max-height: 500px) {
    #hamburger-menu #sidebar-menu {
        height: calc(var(--hamburger-height) * 1.75);
        width: calc(var(--hamburger-width) * .75);
    }
}  