/* NavBar */

/* * {
    border: 1px solid black;
} */


#navContainer{
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;
    display: flex;
    position: fixed;
    font-size: 1.5rem;
    align-items: center;
    color: var(--fg-color);
    height: var(--nav-height);  
    min-height: var(--nav-height);
    justify-content: space-between;
    background: var(--hdr-bg-color);
    backdrop-filter: var(--blur-default);
    box-shadow: 0px -2px 8px -4px rgb(0 0 0 / 80%);
}

#navContainer #header {
    margin-top: 5%;
    margin-left: 3%;
    margin-bottom: 5%;
    cursor: default;
}

#navContainer #header .logo{
    width: 3.5rem;
    height: 2.5rem;
    min-width: 3.5rem;
    min-height: 2.5rem;
    border-radius: 12%;
    box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 1);
    -webkit-box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 1);
    -moz-box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 1);
}

#navContainer h2 {
    color: white;
    font-size: 120%;
    margin-left: 5%;
    text-shadow: 8px 8px 6px black; 
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

#navContainer ul {
    width: 25em;
    padding: 1em 0;
    margin: 5em auto;
    text-align: center;
}

#navContainer li {
    font-size: 80%;
    margin-left: 4%;
    margin-right: 4%;
    display: inline-flex;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

#navContainer .linkContainer li:last-child {
    border-right: 0;
    margin-right: 0;
    padding-right: 0;
}

@media screen and (max-width: 768px) {
      .navLinks {
        display: none;
      }
}