*, *::before, *::after{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --gradient: linear-gradient(300deg, #ba4aff, rgba(186, 74, 255, 0) 70%), 
              linear-gradient(227deg, #008aff, rgba(0, 138, 255, 0) 70%), 
              linear-gradient(104deg, #00ffc6, rgba(0, 255, 198, 0) 74%);
  --nav-height: 6rem;
  --font-color: white;
  --body-bg-color: rgba(32, 32, 32, 1);
  --highlight-primary: gray;
  --blur-default: blur(7px);
  --hamburger-hidden: -100vw;
  --hamburger-show: 0;
  --hamburger-height: 40vh;
  --hamburger-width: 35vw;
  ---fg-color: rgba(255, 255, 255, .9);
  --hdr-bg-color: rgba(255, 255, 255, .0001);
  --ftr-bg-color:  rgba(96, 104, 108, 1);
  --slide-in: slide-in 1.5s ease-out forwards;
  --transition-fast: 100ms ease-out;
  --transition: 250ms ease-out;  
  --transition-long: 500ms ease-out; 
  --transition-longer: 750ms ease-out; 
}

body {
  margin: 0;
  height: 100%;
  min-height: 100%;
  overflow-x: hidden;
  color: var(--font-color);
  background: var(--body-bg-color);
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

li {
  list-style-type: none;
}

img {
  pointer-events: none;
}

/* Slide In */
@keyframes slide-in {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
		transform: translateX(0);
	}
}

/* Firefox < 16 */
@-moz-keyframes slide-in {
  0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
		transform: translateX(0);
	}
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes slide-in {
  0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
		transform: translateX(0);
	}
}

/* Internet Explorer */
@-ms-keyframes slide-in {
  0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
		transform: translateX(0);
	}
}


/* Fade In */
@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Internet Explorer */
@-ms-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}