/* Parallax Effect */
.parallax {
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
}

/* Page Wrapper */
#wrapper {
  overflow: auto;
  min-height: 100vh;
  position: relative;
}

/* Page Container */
.container {
  padding-top: 5%;
  padding-bottom: 8%;
}

/* Dialog */
dialog { 
  display: block;
}

dialog:not(:target):not([open]) { 
  display: none;
}

.dialogClose {
  margin-left: 2%;
  text-decoration: none;
  text-shadow: 1px 1px 8px;
  color: rgb(84, 214, 214);
}

.dialogText {
  padding: .75%;
}

/* Underline Transition */
.underline::after {
  content: '';
  display: block;
  border-radius: 1px;
  border-bottom: solid 3px red;
  transform: scaleX(0);
  transition: var(--transition-long);
}

.underline:hover::after {
  transform: scaleX(.95);
}

/* Link Container */
.linkContainer{
  display: inline-flex;
  justify-content: flex-end;
}

/* Link */
.link{
  color: white;
  font-weight: 500;
  text-decoration: none;
  text-shadow: 2px 2px 2px black;
  transition: var(--transition);
}

.link:hover{
  color: darkgray;
  cursor: pointer;
  text-shadow: 4px 4px 4px black;
}

.link:active{
  text-shadow: 0px 0px 0px black;
}

/* Slide Down */
.slideDown {
  transform: translateY(-50%);
  -moz-animation: var(--slide-in); /* Firefox < 16 */
  -ms-animation: var(--slide-in); /* Internet Explorer */
   -o-animation: var(--slide-in); /* Opera < 12.1 */
      animation: var(--slide-in);
}

/* Slide In */
.slideIn {
  transform: translateX(-200%);
  -moz-animation: var(--slide-in); /* Firefox < 16 */
  -ms-animation: var(--slide-in); /* Internet Explorer */
   -o-animation: var(--slide-in); /* Opera < 12.1 */
      animation: var(--slide-in);
}

/* Fade In */
.fadeIn {
  -webkit-animation: fadein 1s; /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 1s; /* Firefox < 16 */
   -ms-animation: fadein 1s; /* Internet Explorer */
    -o-animation: fadein 1s; /* Opera < 12.1 */
       animation: fadein 1s;
}

/* Zoom */
.zoom {
  transition: transform var(--transition);
}

.zoom:hover {
  transform: scale(1.4);
}

@media screen and (max-width: 768px) {
  .container {
    padding-top: 20%;
    padding-bottom: 25%;
  }

}