/* Contact */
#contact {
    cursor: default;
    text-align: center;
}

#contact .container {
    padding: 0;
    color: Linen;
    margin-top: 15vh;
    text-shadow: 2px 2px 8px black;
    background: rgba(80, 76, 76, .8);
}

#contact .section {
    margin: 4%;
    display: inline-block;
}

#contact .section h1 {
    width: 8em;
    text-align: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

#contact .s1 p {
    width: 30em;
    padding: 1em;
    margin-left: 14%;
    margin-bottom: 5%;
    line-height: 2em;
    text-align: start;
    word-spacing: .15em;
}

#contact .s2 {
    padding-left: .1%;
}

#contact #officeGuy {
    max-width: 18vw;
    max-height: 30vh;
    border-radius: 5%;
    border: 1px solid white;
    box-shadow: inset 1px 1px 10px black;
    background-image: url(./../images/backgrounds/cloudyBlueSky.jpg);
}

@media screen and (max-width: 750px) {
    #contact #officeGuy {
        display: none;
    }
}

@media screen and (max-width: 600px) {
    #contact .s1 p {
        width: 15em;
    }
}